/* eslint-disable react-hooks/exhaustive-deps */

import React, { useState, useEffect } from "react";
import "./ProjectDisplay.scss";
//import Background

const projectDisplayConfiguration = {
  MOBILE: {
    frameName: "phoneframe.png",
    scaleFactor: 0.28,
    frameWidth: 826,
    frameHeight: 1718,
    paddingTop: 192,
    paddingLeft: 38,
    paddingRight: 38,
    paddingBottom: 191,

    // controlPanelHeight: 150,
    buttonSize: 120,
    bottomOffset: 50,

    headerTop: 50,
    headerFontSize: 75,
  },
  WEB: {
    frameName: "webbrowserframe.png",
    scaleFactor: 0.95,
    frameWidth: 719,
    frameHeight: 438,
    paddingTop: 71,
    paddingLeft: 1,
    paddingRight: 1,
    paddingBottom: 51,

    // controlPanelHeight: 100,
    buttonSize: 30,
    bottomOffset: 10,

    headerTop: 1,
    headerFontSize: 25,
  },
};

const ProjectDisplay = ({
  projectType = "MOBILE" | "WEB",
  projectName,
  numOfImages,
}) => {
  let img = new Image();
  const [projSettings, setProjSettings] = useState({});
  const [currentPic, setCurrentPic] = useState(1);
  const [selectedImage, setSelectedImage] = useState(null);
  const [imageLoading, setImageLoading] = useState(true);
  const [backgroundOpacity, setBackgroundOpacity] = useState(1);

  useEffect(() => {
    //set config vars
    let {
      scaleFactor,
      frameName,
      ...otherSettings
    } = projectDisplayConfiguration[projectType];
    let transformedSettings = { frameName };
    for (const [key, value] of Object.entries(otherSettings)) {
      transformedSettings[key] = value * scaleFactor + "px";
    }

    setProjSettings(transformedSettings);
    setImage(currentPic);
  }, []);

  const buttonNext = () => {
    let nextPic = currentPic + 1;
    if (nextPic > numOfImages) return;
    setImage(nextPic);
  };

  const buttonPrevious = () => {
    let nextPic = currentPic - 1;
    if (nextPic < 1) return;
    setImage(nextPic);
  };

  const setImage = (picVal) => {
    setImageLoading(true);
    setBackgroundOpacity(0.3);

    setCurrentPic(picVal);

    img.src = encodeURI(`./Projects/${projectName}/${picVal}.png`);

    img.onload = () => {
      setTimeout(() => {
        setSelectedImage(img.src);
        setImageLoading(false);
        setBackgroundOpacity(1);
      }, 500);
    };
  };

  return (
    <div
      className="ProjectDisplay_Container"
      style={{
        backgroundImage: `url(${projSettings["frameName"]})`,
        // backgroundImage: `url(require(${projSettings["frameName"]}))`,
        width: projSettings["frameWidth"],
        height: projSettings["frameHeight"],
      }}
    >
      <div
        className="ProjectHeader"
        style={{
          top: projSettings["headerTop"],
          fontSize: projSettings["headerFontSize"],
        }}
      >
        {projectName}
      </div>
      <div className="LoadingText">{imageLoading && "LOADING..."}</div>
      <div
        className="ProjectDisplay_Background"
        style={{
          backgroundImage: `url(${selectedImage})`,
          opacity: backgroundOpacity,
          top: projSettings["paddingTop"],
          left: projSettings["paddingLeft"],
          right: projSettings["paddingRight"],
          bottom: projSettings["paddingBottom"],
        }}
      ></div>

      <div
        className="Controls"
        style={{ bottom: projSettings["bottomOffset"] }}
      >
        <div
          className="Controls_Button"
          style={{
            width: projSettings["buttonSize"],
            height: projSettings["buttonSize"],
          }}
          onClick={buttonPrevious}
        >
          <ion-icon name="arrow-back-circle"></ion-icon>
        </div>

        <div className="Controls_Progress">{`${currentPic} / ${numOfImages}`}</div>

        <div
          className="Controls_Button"
          style={{
            width: projSettings["buttonSize"],
            height: projSettings["buttonSize"],
          }}
          onClick={buttonNext}
        >
          <ion-icon name="arrow-forward-circle"></ion-icon>
        </div>
      </div>
    </div>
  );
};

export default ProjectDisplay;
