import React from "react";
import { NavLink } from "react-router-dom";
import "./Links.scss";

const Links = () => {
  const links = [
    { path: "aboutme", iconName: "person-sharp" },
    { path: "projects", iconName: "briefcase-sharp" },
    // { path: "skills", iconName: "library-sharp" },
    { path: "contact", iconName: "call-sharp" },
    { path: "education", iconName: "school-sharp" },
  ];

  return (
    <div className="LinksContainer">
      {links.map((l) => (
        <div className="LinkContainer_Inner" key={l.path.toString()}>
          <NavLink
            to={"/" + l.path}
            className="NavItem"
            activeClassName="NavItemActive"
          >
            <ion-icon name={l.iconName}></ion-icon>
          </NavLink>
        </div>
      ))}
    </div>
  );
};

export default Links;
