import React from "react";
import "./ProjectsScreen.scss";
import PageHeader from "../Components/PageHeader";
import ProjectDisplay from "../Components/ProjectDisplay";

const ProjectsScreen = () => {
  const MOBILE_CONSTANT = "MOBILE";
  const WEB_CONSTANT = "WEB";

  // const

  return (
    <>
      <PageHeader header="PROJECTS" iconName="briefcase">
        {/* Several projects completed which have visual front end */}
      </PageHeader>
      <div>
        {/* <h3>Mobile Projects:</h3> */}
        <div className="ProjectsContainer">
          <ProjectDisplay
            projectType={MOBILE_CONSTANT}
            projectName="Insights Discovery"
            numOfImages={42}
          ></ProjectDisplay>

          <ProjectDisplay
            projectType={MOBILE_CONSTANT}
            projectName="Insights Marketing"
            numOfImages={30}
          ></ProjectDisplay>
        </div>
      </div>

      <div className="Divider"></div>

      <div>
        {/* <h3>Web Projects:</h3> */}
        <ProjectDisplay
          projectType={WEB_CONSTANT}
          projectName="Team Wheel"
          numOfImages={8}
        ></ProjectDisplay>
      </div>
    </>
  );
};

export default ProjectsScreen;
