import React from "react";
import PageHeader from "../Components/PageHeader";
import "./AboutMeScreen.scss";
import SkillList from "../Components/SkillList";

const AboutMeScreen = () => {
  const myTechnicalSkills = [
    { header: "OOP / Scripting", skills: ["JavaScript", "Python", "C#"] },
    { header: "BackEnd", skills: ["Node", "Flask", ".NET"] },
    {
      header: "FrontEnd",
      skills: ["React", "Angular 2+", "ASP MVC", "HTML | S(CSS) | JS"],
    },
    { header: "Databases", skills: ["SQL (MSQL)", "NoSQL (Mongo)"] },
    { header: "Mobile", skills: ["React Native / Expo", "Appcelerator"] },
    { header: "Contenerization", skills: ["Docker"] },
    { header: "Source Control", skills: ["Git", "GitLab", "TFS"] },
    { header: "Package Managers", skills: ["NPM", "Nuget"] },
    { header: "Testing", skills: ["Jest", "Mocha", "Jasmin", "NUnit"] },
  ];

  const myOtherActivities = [
    { header: "Sport Activities", skills: ["Calisthenics", "Weight Lifting"] },
    { header: "Gaming (PC)", skills: ["Path Of Exile", "Witcher 3", "Wolcen"] },
    { header: "Home Automation", skills: ["Raspberry PI", "HASS IO"] },
  ];
  return (
    <>
      <PageHeader header="ABOUT ME" iconName="person">
        {/* <article>Short and simple about me...</article> */}
        {/* <article>Software Developer: [Web,Mobile,Desktop]</article> */}
        {/* <article>Sports: [Calisthenics, Weight Lifting]</article> */}
      </PageHeader>
      <div>
        {/* <h4>Technical Skills:</h4> */}
        <div className="TechincalSkillsContainer">
          {myTechnicalSkills.map((s) => (
            <SkillList
              key={s.header.toString()}
              header={s.header}
              skills={s.skills}
            />
          ))}
        </div>
      </div>

      <div className="Divider"></div>

      <div>
        {/* <h4>Other Activities:</h4> */}
        <div className="TechincalSkillsContainer">
          {myOtherActivities.map((s) => (
            <SkillList
              key={s.header.toString()}
              header={s.header}
              skills={s.skills}
            />
          ))}
        </div>
      </div>
    </>
  );
};

export default AboutMeScreen;
